
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

@charset "UTF-8";
.showRouter-enter-active {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.showRouter-leave-active {
  -webkit-transition: all 0 ease;
  transition: all 0 ease;
}
.showRouter-enter,
.showRouter-leave-active {
  -webkit-transform: translateX(-150px);
          transform: translateX(-150px);
  opacity: 0;
}
.flex {
  -webkit-box-flex: 8;
      -ms-flex: 8;
          flex: 8;
}
.header {
  /* Safari 5.1 - 6.0 */
  /* Opera 11.1 - 12.0 */
  /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, right top, from(#fd9f55), to(#ff5079));
  background: linear-gradient(to right, #fd9f55, #ff5079);
  /* 标准的语法（必须放在最后） */
}
.header .title {
    color: #fff;
    padding-top: 10px;
    text-align: center;
}
.header .cover {
    width: 80px;
    height: 80px;
    float: left;
    margin-left: 20px;
    margin-bottom: 10px;
    border-radius: 5px;
    margin-right: 10px;
}
.header .info {
    text-align: left;
    list-style: none;
    color: #fff;
}
.header .info .name {
    padding-top: 5px;
    font-size: 18px;
}
.header .info .user {
    margin-top: 5px;
    font-size: 14px;
    opacity: 0.9;
}
.header .info .desc {
    margin-top: 2px;
    font-size: 14px;
    opacity: 0.9;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
.footer {
  position: fixed;
  background-color: #222;
  bottom: 0px;
  left: 0px;
  width: 100%;
  z-index: 9999;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
}
.footer .button {
    border: 2px solid #ff5079;
    border-radius: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    display: inline-block;
}
.footer .open {
    width: 40%;
    background: #fff;
    color: #ff5079;
    float: left;
}
.footer .download {
    width: 40%;
    background: #ff5079;
    color: #fff;
}
.music-list {
  padding-top: 4px;
  padding-left: 4px;
  padding-right: 4px;
  margin-bottom: 85px;
  overflow: auto;
}
.music-list .music-item + .music-item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.music-list .music-item {
    padding: 4px 6px 0 6px;
    position: relative;
    margin-bottom: 4px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
}
.music-list .music-item .music-img {
      width: 50px;
      height: 50px;
      border-radius: 5px;
}
.music-list .music-item span.music-name {
      display: inline-block;
      width: 70%;
      vertical-align: top;
      margin-top: 15px;
      margin-left: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: left;
      cursor: pointer;
      padding-bottom: 20px;
}
